import React, { useState } from "react";
import * as S from "./styles";
import CheckBox from "../Form/Checkbox";
import IconInput from "./IconInput";
import Image from "../../assets/img/local.png";
import Select from "../Form/Select";
import TogglerLink from "../Form/TogglerLink";
import ButtonsForm from "../Form/ButtonsForm/index";

const Form = (props) => {
  const [year, setYear] = useState();
  const [model, setModel] = useState();
  const [brand, setBrand] = useState();

  const onChangeYear = (option) => {
    setYear(option.value);
  };

  const onChangeModel = (option) => {
    setModel(option.value);
  };

  const onChangeBrand = (option) => {
    setBrand(option.value);
  };

  return (
    <S.container className="containerForm">
      <S.Row>
        <S.containerCheckBox className="containerCheckBox">
          <CheckBox title="Novos" />
          <CheckBox title="Usados" checked />
        </S.containerCheckBox>
      </S.Row>

      <S.Row>
        <S.containerItems className="containerItems">
          <S.Row>
            <S.Col>
              <IconInput icon={Image} />
            </S.Col>
          </S.Row>

          <S.Row>
            <S.Col>
              <Select
                placeholder="Ano Desejado"
                label="Ano"
                onChange={onChangeYear}
                value={year}
                options={[
                  { label: "2020", value: 2020 },
                  { label: "2019", value: 2019 },
                  { label: "2018", value: 2018 },
                ]}
              />
            </S.Col>

            <S.Col>
              <Select placeholder="Faixa de Preço" />
            </S.Col>
          </S.Row>
        </S.containerItems>

        <S.containerItems className="containerItems">
          <S.Row>
            <S.Col>
              <Select
                label="Marca"
                placeholder="Marca:"
                value={brand}
                options={props.brands}
                onChange={onChangeBrand}
              />
            </S.Col>

            <S.Col>
              <Select
                label="Modelo"
                onChange={onChangeModel}
                options={props.models}
                value={model}
                placeholder="Modelo:"
              />
            </S.Col>
          </S.Row>
          <S.Row>
            <S.Col>
              <Select placeholder="Versão:" />
            </S.Col>
          </S.Row>
        </S.containerItems>
      </S.Row>

      <S.Row>
        <S.Col>
          <TogglerLink>Busca Avançada</TogglerLink>
        </S.Col>
        <S.Col>
          <ButtonsForm />
        </S.Col>
      </S.Row>
    </S.container>
  );
};

export default Form;
